import axios from 'axios';
class Mourits {
    constructor() { 
    }
}
Mourits.getlyrics = async(artist, song) => {
    let res = await axios({
        method: 'get',
        url: 'https://mourits-lyrics.p.rapidapi.com/',
        withCredentials: false,
        headers: {
            "x-rapidapi-key": "7750b3e277msh0b52bb42d9ed05cp19fff0jsn13a56896b4ec",
	        "x-rapidapi-host": "mourits-lyrics.p.rapidapi.com",
	        "useQueryString": true
        },
        params: {
            artist: artist,
            song: song
        }
    });
    return res;
}

export default Mourits;