class Country {
    constructor() { 
    }
}
   
Country.answers = ['Green Tractor', 'Satellite', "God's Country", 'Heaven', 'Life With You', 'Time is Love', "Livin'", "She's Like Texas",
'A Girl Like You', 'Runaway', 'Rainbow', 'Love Story', 'The Heart Wants What it Wants', 'You Get It', 'I Go Back', 'With You I Am', 'You and Tequila', 
'Tonight', 'My Band', 'Better Together', 'Wave on Wave', 'Wish That You Would', 'Closer', 'Too Late For Goodbye', 'We Are Tonight', 'Still Into You',
'Diamond Rings and Old Barstools', 'Sorry', 'My Kinda Bar', 'Southbound', 'Wild Ones', 'Homegrown', 'The Way I Talk', 'Circles', 'Stay', 'Tie Me Down', 'Close',
"That's the Truth", "All Your'n", 'Remedy', 'To You', 'My Way', 'Long Nights', 'Rodeo', 'She Will', 'Dream', "Trippin", 'Nights With You', "Kiss Me", "Austin", 
'Stomping Ground', 'Ride With Me', 'Housefire', 'Where I Grew Up', "What Life's About", 'Fuss & Fight', "Drinkin' Problem", 'Landslide', "Thinking 'Bout You", 'Burn Out', 'Mine', 'It Will Rain', 'Needed', 
'Misunderstood']

Country.getanswers = (index, song) => {
    let used = [];
    let a = [];
    a[index] = song;
    index++;
    while (used.length < 3) {
      if (index == 4) {
        index = 0;
      }
      let rand = Math.floor(Math.random() * 64);
      // change to length of array i made ^^
      while (used.includes(rand)) {
        rand = Math.floor(Math.random() * 4);
      }
      a[index] = Country.answers[rand];
      used.push(rand);
      index++;
    }
    return a;
}


export default Country;