import axios from 'axios';
class Shazam {
    constructor() { 
    }
}
Shazam.autocomplete = async(value) => {
    let res = await axios({
        method: 'get',
        url: 'https://shazam.p.rapidapi.com/auto-complete',
        withCredentials: false,
        headers: {
            "x-rapidapi-key": "9eed5b451dmsh0cfeffd45d17ff2p1f9bd5jsn77e7366efadc",
	        "x-rapidapi-host": "shazam.p.rapidapi.com",
	        "useQueryString": true
        },
        params: {
            term: value,
            locale: 'en-US'
        }
    });
    return res;
}

Shazam.search = async(value) => {
    let res = await axios({
        method: 'get',
        url: 'https://shazam.p.rapidapi.com/search',
        withCredentials: false,
        headers: {
            "x-rapidapi-key": "9eed5b451dmsh0cfeffd45d17ff2p1f9bd5jsn77e7366efadc",
	        "x-rapidapi-host": "shazam.p.rapidapi.com",
	        "useQueryString": true
        },
        params: {
            term: value,
            locale: 'en-US',
            offset: 0,
	        limit: 5
        }
    });
    return res;
}

Shazam.songdetails = async(value) => {
    let res = await axios({
        method: 'get',
        url: 'https://shazam.p.rapidapi.com/songs/get-details',
        withCredentials: false,
        headers: {
            "x-rapidapi-key": "9eed5b451dmsh0cfeffd45d17ff2p1f9bd5jsn77e7366efadc",
            "x-rapidapi-host": "shazam.p.rapidapi.com",
            "useQueryString": true
        },
        params: {
            key: value,
            locale: 'en-US'
        }
    });
    return res;
}

Shazam.getrelated = async(value) => {
    let res = await axios({
        method: 'get',
        url: 'https://shazam.p.rapidapi.com/songs/list-recommendations',
        withCredentials: false,
        headers: {
            "x-rapidapi-key": "9eed5b451dmsh0cfeffd45d17ff2p1f9bd5jsn77e7366efadc",
	        "x-rapidapi-host": "shazam.p.rapidapi.com",
	        "useQueryString": true
        },
        params: {
            key: value,
            locale: 'en-US'
        }
    });
    return res;
}



export default Shazam;