class Pop {
    constructor() { 
    }
}
   
Pop.answers = ['Time', 'All Falls Down (feat. Odessa)', 'Love me', 'Heartbroken', 'Carry On', 'Life', 'Just Livin (feat. DaBaby)', '400 times',
'Up to You', 'A Girl Can Wish', 'Sleeping (Remix)', 'Love Story', 'The Heart Wants What it Wants', 'Save Your Tears (Remix)', 'The A Team', 'Despacito (Remix)', 'Havana (feat. Young Thug)', 
'Roses (Remix)', 'Champagne and Pools', "Caroline", 'Waves', 'Wish That You Would', 'Closer', 'Deja Vu', 'Hard Times', 'Still Into You',
'Like a G6', 'Need You Right Now', 'Cheap Thrills', 'New Rules', 'Wild Ones', 'Awake (Remix)', 'Dancing on my Own', 'Circles', 'Stay', 'Tie Me Down', 'Close',
"That's the Truth", 'Runaway', 'Remedy', 'To You', 'My Way', 'Long Nights', 'Bring It', 'She Will', 'Dream', 'Capsize', 'Nights With You', 'Tongue Tied', 'BRIGHT', 
'Sugar', 'Cheerleader', 'Love to Lay', 'Take Me Home', 'No Air', 'Sedated', 'Misery', "Can't Stop", 'Me and My Broken Heart', 'Reminder', 'XO', 'It Will Rain', 'Needed', 
'Consideration (feat. SZA)']

Pop.getanswers = (index, song) => {
    let used = [];
    let a = [];
    a[index] = song;
    index++;
    while (used.length < 3) {
      if (index == 4) {
        index = 0;
      }
      let rand = Math.floor(Math.random() * 64);
      // change to length of array i made ^^
      while (used.includes(rand)) {
        rand = Math.floor(Math.random() * 4);
      }
      a[index] = Pop.answers[rand];
      used.push(rand);
      index++;
    }
    return a;
}


export default Pop;
