Deezer.getpopplaylist = async() => {
    let r = await axios({
        method: 'get',
        url: "https://deezerdevs-deezer.p.rapidapi.com/playlist/8732950902",
        withCredentials: false,
        headers: {
            "x-rapidapi-key": "7750b3e277msh0b52bb42d9ed05cp19fff0jsn13a56896b4ec",
	        "x-rapidapi-host": "deezerdevs-deezer.p.rapidapi.com",
	        "useQueryString": true
        },
        params: {
            id: 8732950902
        }
    });
    let playlist = r
        let tracks = [];
        let used = [];
        let index = 0;
        for (let i = 0; i < 5; i++) {
          index = Math.floor(Math.random() * 52);
          while (used.includes(index) || playlist.data.tracks.data[index].artist.name == 'Alexandra Stan') {
              index = Math.floor(Math.random() * 52);
          }
          used.push(index);
          tracks.push(playlist.data.tracks.data[index]);
        }
        return Array.from(tracks);
}

