class Seventies {
    constructor() { 
    }
}
   
Seventies.answers = ['Boogie', 'More Than a Woman', "Rhiannon (Remaster)", 'Little Lies', 'Dancing in the Dark', 'Night Fever', "How Deep is Your Love'", "ABC",
"Who's Loving You", 'Runaway', 'Jumpin', 'Vienna', 'Uptown Girl', 'She Drives Me Crazy', "We Didn't Start the Fire", "She's Always a Woman (Remaster)", 'Rocket Man', 
"Like a Prayer", 'Your Song', "Don't Go Breaking My Heart", 'Wave on Wave', "I Won't Back Down", "Runnin' Down a Dream", 'Wildflowers', 'In the Night', "Don't Go Yet",
'Come Together', 'Yellow Submarine', 'Let it Be', 'Yesterday', 'Hey Jude', 'American Girl', 'Refugee', 'Learning to Fly', 'Breakdown', 'Fly Like an Eagle', 'Take the Money and Run',
"That's the Truth", "Jet Airliner", 'Remedy', 'To You', 'My Way', 'Long Nights', 'Rodeo', "Rock'n Me", 'Dream', "Say You Love Me", 'Edge of Seventeen (Remaster)', "Crazy on You", "Somebody to Love", 
"What's Love Got to Do With It", 'Under Pressure', 'We Will Rock You', 'Take it Easy', "Life in the Fast Lane", 'Highway to Hell', "One of These Nights", 'Landslide', 'You Shook Me All Night Long', 'Burn Out', 'Mine', 'Thunderstruck', "Girls", 
'Misunderstood']

Seventies.getanswers = (index, song) => {
    let used = [];
    let a = [];
    a[index] = song;
    index++;
    while (used.length < 3) {
      if (index == 4) {
        index = 0;
      }
      let rand = Math.floor(Math.random() * 64);
      // change to length of array i made ^^
      while (used.includes(rand)) {
        rand = Math.floor(Math.random() * 4);
      }
      a[index] = Seventies.answers[rand];
      used.push(rand);
      index++;
    }
    return a;
}


export default Seventies;