class Levels extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      genre: "pop",
      length: 20
    }
    this.gametime = this.gametime.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handlelength = this.handlelength.bind(this);
    this.home = this.home.bind(this);
  }

  async gametime() {
    if (this.state.genre == 'pop') {
      Deezer.getpopplaylist().then((tracks) => ReactDOM.render(<Game tracks = {tracks} length= {this.state.length} genre = {this.state.genre}/>, document.getElementById('app')))
    }
    if (this.state.genre == 'rap') {
      Deezer.getrapplaylist().then((tracks) => ReactDOM.render(<Game tracks = {tracks} length= {this.state.length} genre = {this.state.genre}/>, document.getElementById('app')))
    }
    if (this.state.genre == '70s') {
      Deezer.get70splaylist().then((tracks) => ReactDOM.render(<Game tracks = {tracks} length= {this.state.length} genre = {this.state.genre}/>, document.getElementById('app')))
    }
    if (this.state.genre == 'country') {
      Deezer.getcountryplaylist().then((tracks) => ReactDOM.render(<Game tracks = {tracks} length= {this.state.length} genre = {this.state.genre}/>, document.getElementById('app')))
    }
    if (this.state.genre == '80s') {
      Deezer.get80splaylist().then((tracks) => ReactDOM.render(<Game tracks = {tracks} length= {this.state.length} genre = {this.state.genre}/>, document.getElementById('app')))
    }
    
  }

  handlelength(event) {
    let l = event.target.value;
    this.setState({length: l})
  }

  handleChange(event) {
    let g = event.target.value;
    this.setState({genre: g})
  }

  home() {
    ReactDOM.render(<App />, document.getElementById('app'));
  }
 
  render() {
    return(
    <div id = "levels">
      <div id = "bar">
      <button class = "home" onClick = {this.home}>Home</button>
      <h1 class = "bartitle">Guess The Song!</h1>
      </div>
      
      
      <h2 class = "instruct" id = "selectlevel">Select the Level (Length of Song Clip)</h2>
      <select value = {this.state.length} onChange = {this.handlelength}>
        <option value = {20}>Level 1: 20 Seconds</option>
        <option value = {10}>Level 2: 10 Seconds</option>
        <option value = {5}>Level 3: 5 seconds</option>
      </select>

      <h2 class = "instruct">Select the Genre</h2>
     
     <select value = {this.state.genre} onChange = {this.handleChange}>
        <option value = "pop">Pop</option>
        <option value = "rap">Rap</option>
        <option value = "country">Country</option>
        <option value = "70s">70s</option>
        <option value = "80s">80s</option>
      </select>
      
      <button class = "play" onClick = {this.gametime}>Let's play!</button>
    </div>)
    }
}

class Game extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      score: 0,
      question: 0,
      songs: this.props.tracks,
      countdown: false
    }
    this.nextquestion = this.nextquestion.bind(this);
    this.wrong = this.wrong.bind(this);
    this.right = this.right.bind(this);
    this.counting = this.counting.bind(this);
    this.gameover = this.gameover.bind(this);
  }
  
  nextquestion() {
    this.setState((prev) => ({countdown: false, question: prev.question + 1}))
  }

  wrong() {
    if (this.state.score >= 10) {
      this.setState((prev) => ({score: prev.score - 10}))
    }
  }

  right() {
    this.setState((prev) => ({score: prev.score + 20}))
    this.counting()
  }

  counting() {
    if (this.state.question == 4) {
      this.nextquestion();
      return;
    }
    this.setState({countdown: true})
  }

  getLevels() {
    ReactDOM.render(<Levels />, document.getElementById("app"))
  }

  getHome() {
    ReactDOM.render(<App />, document.getElementById('app'))
  }

  gameover() {
    this.setState({question: 5});
  }

  render() {
    let index = this.state.question;

    

    if (this.state.question == 5) {
      return(<div class = "gameover">
         <div id = "bar">
         <button class = "home" onClick = {this.getHome}>Home</button>
        <h1 class = "bartitle">Guess The Song!</h1>
        </div>
        <h1 id = "gameover">Game Over</h1>
        <h1>Score: {this.state.score}</h1>
        <button id = "playagain" onClick = {this.getLevels}>Play Again</button>
        
      </div>)
    }

    if (this.state.countdown) {
      return (<div>
        <div id = "bar">
        <button class = "home" onClick = {this.getHome}>Home</button>
        <h1 class = "bartitle">Guess The Song!</h1>
        <button class = "quit" onClick = {this.gameover}>Quit</button>
        </div>
         <h1 class = "question">Question {this.state.question + 1} / 5</h1>
        <h1 class = "score">Score: {this.state.score}</h1>
        <CountDown onzero = {this.nextquestion} />
      </div>)
    }

    return(
      <div id ="question">
         <div id = "bar">
      <button class = "home" onClick = {this.getHome}>Home</button>
      <h1 class = "bartitle">Guess The Song!</h1>
      <button class = "quit" onClick = {this.gameover}>Quit</button>
      </div>
      <h1 class = "question">Question {this.state.question + 1} / 5</h1>
      
      <h1 class = "score">Score: {this.state.score}</h1>
      <ReactPlayer
        url = {this.state.songs[this.state.question].preview}
        width="1px"
        height="1px"
        playing={true}
        controls={false}
        
      />
      <Timer count = {this.props.length} onzero = {this.counting} num = {this.state.question}/>
      
      <Answers correct = {this.state.songs[this.state.question].title} onwrong = {this.wrong} onright = {this.right} genre = {this.props.genre}/>
      </div>
    )
  }
}

class Answers extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      answers: [],
      correct: -1,
    }
    this.clicked = this.clicked.bind(this);
  }

  mount() {
    if (this.props.genre == 'pop') {
      let index = Math.floor(Math.random() * 4);
      let a = Pop.getanswers(index, this.props.correct);
      this.setState({answers: a, correct: index})
    }
    if (this.props.genre == 'country') {
      let index = Math.floor(Math.random() * 4);
      let a = Country.getanswers(index, this.props.correct);
      this.setState({answers: a, correct: index})
    }
    if (this.props.genre == '70s' || this.props.genre == '80s') {
      let index = Math.floor(Math.random() * 4);
      let a = Seventies.getanswers(index, this.props.correct);
      this.setState({answers: a, correct: index})
    }
    if (this.props.genre == 'rap') {
      let index = Math.floor(Math.random() * 4);
      let a = Rap.getanswers(index, this.props.correct);
      this.setState({answers: a, correct: index})
    }
  }

  clicked(event) {
    let index = event.target.getAttribute('data-index')
    if (index == this.state.correct) {
      this.props.onright();
    } else {
      this.props.onwrong();
      event.target.style = "visibility: hidden"
      this.state.answers[index] = "";
    }
  }

  render() {
    if (this.props.correct != this.state.answers[this.state.correct]) {
      this.mount()
    }
    
    return (
    <div class = 'answers'>
      <button class = "answer" data-index = {0} onClick = {this.clicked}>{this.state.answers[0]}</button>
      <button class = "answer" data-index = {1} onClick = {this.clicked}>{this.state.answers[1]}</button>
      <button class = "answer" data-index = {2} onClick = {this.clicked}>{this.state.answers[2]}</button>
      <button class = "answer" data-index = {3} onClick = {this.clicked}>{this.state.answers[3]}</button>
    </div>)
  }
}

class Timer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      count: -10,
      num: -10
    }
  }

  componentDidMount() {
    const startcount = this.props.count
    this.setState({count: startcount, num: this.props.num})

    this.myInterval = setInterval(() => {
      this.setState((prev) => ({count: prev.count - 1}))
    }, 1000)
  }

  render() {
    let start = this.props.count

    if (this.state.num != this.props.num) {
      this.setState({count: start, num: this.props.num})
    }
    
    if (this.state.count == 0) {
      this.setState({count: start})
      this.props.onzero();
    }
    return(<div>
      <h1 class = "timercount">{this.state.count}</h1></div>
    )
  }
}


class CountDown extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      count: -10,
      num: -10
    }
  }

  componentDidMount() {
    const startcount = 3
    this.setState({count: startcount})

    this.myInterval = setInterval(() => {
      this.setState((prev) => ({count: prev.count - 1}))
    }, 1000)
  }

  render() {
    let start = 3
/*
    if (this.state.num != this.props.num) {
      this.setState({count: start, num: this.props.num})
    }
    */
    
    if (this.state.count == 0) {
      this.setState({count: start})
      this.props.onzero();
    }
    return(<div>
      <h1 class = "nextin">Next Question In... </h1>
      <h1>{this.state.count}</h1>
    </div>)
  }
}



  class SearchBox extends React.Component{
    constructor(props) {
      super(props);
      this.state = {
        searchInput: "",
        matches: [],
        typing: false,
        loading: false
      }
      this.clicked = this.clicked.bind(this);
    }
   
    handleInputChange = (event) => {
        event.preventDefault()
        let input = event.target.value;
        this.setState({searchInput: input});
        if (input == "") {
          this.setState({searchInput: input, matches: [], typing: false})
        } else {
          Shazam.autocomplete(input).then((r) => {
            let m = Array.from(r.data.hints)
            this.setState({matches: m, typing: true})
          })
      }
    }

    clicked(event) {
      this.setState({matches: [], loading: true})
      let t = event.target.getAttribute('data-term');
      ReactDOM.render(<Search term = {t}/>, document.getElementById('app'));
    }
    
    render() {

      if (this.state.loading) {
        return <h1>Loading...</h1>
      }

      return (
          <div>
              <input 
                  placeholder="Search for a song..." 
                  onChange = {this.handleInputChange} 
                  value = {this.state.searchInput}
              />
              <div class = "matches">
              {this.state.matches.map((r) => {
                return(<button class = "match" data-term = {r.term} onClick = {this.clicked}>{r.term}</button>)})}
              </div>
          </div>
      );
    }
  }

  class Search extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        term: this.props.t,
        tracks: [],
        loading: true
      }
      this.trackclicked = this.trackclicked.bind(this);
    }

    componentDidMount() {
      Shazam.search(this.props.term).then((r) => {
        let tracks = Array.from(r.data.tracks.hits)
        let artists = Array.from(r.data.artists.hits)
        this.setState({term: this.props.term, tracks: tracks, artists: artists, loading: false})
      })
    }

    trackclicked(event) {
      let key = event.target.getAttribute('data-id');
      Shazam.songdetails(key).then((r) => ReactDOM.render(<TrackPage r = {r}/>, document.getElementById('app')));
    }

    home() {
      ReactDOM.render(<App />, document.getElementById('app'))
    }

    render() {
      {if (this.state.loading) {
        return(
        <div class = "results">
          <div id = "bar">
        <button class = "home" onClick = {this.home}>Home</button>
        <SearchBox />
        </div>
        <h1>Results</h1>
        <h1>Loading...</h1>
        </div>)
      }

      return (
      <div class = "results">
        <div id = "bar">
        <button class = "home" onClick = {this.home}>Home</button>
        <SearchBox />
        </div>
        <h1>Results</h1>
          {this.state.tracks.map((r) => {
                return(<a class = "result" data-id = {r.track.key} onClick = {this.trackclicked}>{r.track.title} By {r.track.subtitle}</a>)})}
      </div>
      )
        }
    }
  }

 class TrackPage extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        title: this.props.r.title,
        subtitle: this.props.r.subtitle,
        genre: this.props.r.data.genres.primary
      }
      this.getrelated = this.getrelated.bind(this);
      this.home = this.home.bind(this);
    }

    getrelated() {
      Shazam.getrelated(this.props.r.data.key).then((r) => {
        ReactDOM.render(<Related list = {r.data.tracks}/>, document.getElementById('related'))})
    }

    home() {
      ReactDOM.render(<App />, document.getElementById('app'))
    }

    render() {
      let vid = "0"
      for (let i = 0; i < this.props.r.data.sections.length; i++) {
        if (this.props.r.data.sections[i].type == 'VIDEO') {
          vid = this.props.r.data.sections[i]['youtubeurl'].actions[0].uri;
        }
      }
      vid = "http://youtube.com/embed/"+ vid.substring(17,vid.length - 11);
      if (vid == "0") {
        return(<div class= "track">
          <div id = "bar">
        <button class = "home" onClick = {this.home}>Home</button>
        <SearchBox />
        </div>
        <img class = "coverart" src = {this.props.r.data.images.coverart}></img>
        <h1 class = "data">Title: <span>{this.props.r.data.title}</span></h1>
        <h1 class = "data">Artist: <span>{this.props.r.data.subtitle}</span></h1>
        <h1 class = "data">Genre: <span>{this.props.r.data.genres.primary}</span></h1>
        <button class= "getrelated" onClick = {this.getrelated}>Get Related Songs</button>
        <div id = "related"></div>
      </div>)
      }
      return(
        <div class= "track">
          <div id = "bar">
          <button class = "home" onClick = {this.home}>Home</button>
          <SearchBox />
          </div>
          <img class = "coverart" src = {this.props.r.data.images.coverart}></img>
          <h1 class = "data">Title: <span>{this.props.r.data.title}</span></h1>
          <h1 class = "data">Artist: <span>{this.props.r.data.subtitle}</span></h1>
          <h1 class = "data">Genre: <span>{this.props.r.data.genres.primary}</span></h1>
          <iframe width="420" height="345" src={vid}>
          </iframe>
          <button class= "getrelated" onClick = {this.getrelated}>Get Related Songs</button>
          <div id = "related"></div>
        </div>
      )
    }
  }

  class Related extends React.Component {
    constructor(props) {
      super(props)
      this.trackclicked = this.trackclicked.bind(this);
    }

    trackclicked(event) {
       let key = event.target.getAttribute('data-id');
       ReactDOM.render(<p id = "related"></p>, document.getElementById('related'));
       Shazam.songdetails(key).then((r) => ReactDOM.render(<TrackPage r = {r}/>, document.getElementById('app')));
     }

    render() {
      if (this.props.list == undefined) {
        return (<div>
          <h1>Related Songs</h1>
          <h2>None Found</h2>
          </div>)
      }
      return(
        <div>
        <h1>Related Songs</h1>
        {this.props.list.map((r) => {
            return(<a class = "result" data-id = {r.key} onClick = {this.trackclicked}>{r.title}</a>)})}
        </div>)
    }
    
  }
 
export default App;
